import { Component} from '@angular/core';
import { NavController, AlertController, ToastController, LoadingController, Platform, TabHighlight} from 'ionic-angular';
import * as firebase from 'firebase';
import { ProfilePage } from '../profile/profile';
import { TabsPage } from '../tabs/tabs';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  verificationId: any;
  code: string = "";
  isExist: boolean = false;
  url:string;
  timer:number = 60;

  public recaptchaVerifier: firebase.auth.RecaptchaVerifier;
  phoneNumber: any;
  password: any;

  splash = true;

  constructor(public navCtrl: NavController, public alertCtrl: AlertController, public toastCtrl: ToastController,
              public loadingCtrl: LoadingController, public platform: Platform) {
      
  }

  ionViewDidLoad(){
    // Recaptcha is initilaizing
    this.recaptchaVerifier = new firebase.auth.RecaptchaVerifier('recaptcha-container', {
      'size': 'invisible',
      'callback': function(response) {
        console.log(response);
      }
    });
  }

  ionViewWillEnter() {
    firebase.auth().onAuthStateChanged((user)=>{
      if(user){
        this.navCtrl.push(TabsPage).then(() => {
          const index = this.navCtrl.getActive().index;
          this.navCtrl.remove(0, index);
        });
      }
    });
  }

  loginUser(){
    let ph = String(this.phoneNumber);
    let ccode_phone = '+91'+this.phoneNumber;

    if(ph == "undefined"){
      const alert = this.alertCtrl.create({
        title: 'Error',
        subTitle: 'Phone number is required.',
        buttons: ['OK']
      });
      alert.present();
      return;
    }else if(ph.length != 10) {
      const alert = this.alertCtrl.create({
        title: 'Error',
        subTitle: 'Invalid Phone number.',
        buttons: ['OK']
      });
      alert.present();
      return;
    }

    const appVerifier = this.recaptchaVerifier;
    firebase.auth().signInWithPhoneNumber(ccode_phone, appVerifier).then((onfulfilled)=>{
      let tCtrl = this.toastCtrl.create({
        message: 'OTP has been sent to your mobile.',
        duration:2000
      });
      tCtrl.present();
      let prompt = this.alertCtrl.create({
        title: 'Enter the Confirmation Code',
        'enableBackdropDismiss':false,
        inputs:[{ name: 'confirmationCode', placeholder: 'Confirmation Code' }],
        buttons: [
          {text:'Cancel', handler: () =>{ console.log('User clicked on cancel button, Nothing is happening now.')}},
          {text: 'Send', handler: (data) => {
            onfulfilled.confirm(data.confirmationCode).then(() =>{
              firebase.database().ref('userDetails').orderByChild('mobileNo').equalTo(ccode_phone).on('child_added', ()=>{
                this.isExist = true;
                this.navCtrl.push(TabsPage).then(()=>{
                  const index = this.navCtrl.getActive().index;
                  this.navCtrl.remove(0, index);
                });
              });
              setTimeout(()=>{
                if(!this.isExist){
                  this.navCtrl.push(ProfilePage);
                }
              }, 3000);
            }, (onRejected)=>{
              if(onRejected.code == "auth/user-disabled"){
                const aCtrl = this.alertCtrl.create({
                  'title': 'Account Disabled',
                  'message': 'Your account is disabled by the administrator.',
                  'enableBackdropDismiss':false,
                  'buttons':[{'text': 'Ok', 'handler': ()=>{ this.platform.exitApp()}}]
                });
                aCtrl.present();
              } else if(onRejected.code == "auth/too-many-requests") {
                const aCtrl = this.alertCtrl.create({
                  'title': 'Account Disabled',
                  'message': 'Your account is disabled by the administrator.',
                  'enableBackdropDismiss':false,
                  'buttons':[{'text': 'Ok', 'handler': ()=>{ this.platform.exitApp()}}]
                });
                aCtrl.present();
              }
            });
          }
        }]
      });
      prompt.present();
    });
  }
}


