import { Component} from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import * as firebase from 'firebase';

/**
 * Generated class for the SpeakersPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-speakers',
  templateUrl: 'speakers.html',
})
export class SpeakersPage{

  eventData:Object = {};
  eventId:String = '';
  speakerIds:Array<String> = [];
  speakersData:Array<Object> = [];
  lctrl;

  constructor(public navCtrl: NavController, public navParams: NavParams, public loadCtrl:LoadingController) {
    this.eventData = this.navParams.get('eventData');
    this.eventId = this.eventData['eventid'];
    this.lctrl = this.loadCtrl.create({
      content:'Please Wait...'
    });
    this.lctrl.present();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SpeakersPage');
  }

  ionViewWillEnter(){
    this.getSpeakersIds(this.eventId);
  }

  getSpeakersIds(evid){
    let that = this;
    let eventid = evid;
    firebase.database().ref('agendaDetails').orderByChild('event_id').equalTo(eventid).on('child_added', function(datasnapshot){
      if(datasnapshot.val().speaker_id.length > 0){
        that.speakerIds = datasnapshot.val().speaker_id;
        that.getSpeakerDetails(that.speakerIds);
      }
    });
  }

  getSpeakerDetails(speakerIds){
    let that = this;
    let ids = speakerIds[0];
    that.speakersData=[];
    ids.forEach(id=> {
      firebase.database().ref('speakersDetails').orderByKey().equalTo(id).on('child_added', function(datasnapshot){
        let speakerDetails = datasnapshot.val();
        that.speakersData.push(speakerDetails);
      });
    });
    setTimeout(() =>{
      that.lctrl.dismiss();
    }, 1000);
  }





}



