import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import * as firebase from 'firebase';

/**
 * Generated class for the AgendaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-agenda',
  templateUrl: 'agenda.html',
})
export class AgendaPage {
  
  eventData: Object = {};
  agendaData: Object = {};
  sessionData:Array<Object> = [];
  sessionFlag:Boolean = false;
  lctrl;
  constructor(public navCtrl: NavController, public navParams: NavParams, public loadCtrl: LoadingController) {
    this.eventData = this.navParams.get('eventData');
    this.lctrl = this.loadCtrl.create({
      content:'Please Wait...'
    });
    this.lctrl.present();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AgendaPage');
  }

  ionViewWillEnter(){
    this.getAgendaDetails(this.eventData, this.lctrl);
  }

  getAgendaDetails(event, lctrl) {
    let eve = event;
    let that = this;
    firebase.database().ref('agendaDetails').orderByChild('event_id').equalTo(eve.eventid).on('child_added', function(datasnap){
      that.agendaData = datasnap.val();
      that.sessionData = that.agendaData['session_data'];
      that.getSessionData(that.sessionData);
      that.agendaData = that.agendaData;
    });
  }

  getSessionData(sess) {
    this.sessionData = [];
    let session = sess;
    let that = this;
    let fname, lname;
    let speakers_list = [];
    for(let s in session){
      session[s]['speakers_list'] = [];
      for(let sp of session[s].speaker){
        firebase.database().ref('speakersDetails').orderByKey().equalTo(sp).on('child_added', function(datasnap){
          fname = datasnap.val().firstName;
          lname = datasnap.val().lastName;
          let name = fname+ ' '+ lname;
          session[s]['speakers_list'].push(name);
        });
      }
      session[s]['startTime'] = new Date(new Date(session[s]["startTime"]).getTime()).toLocaleString();
      session[s]['endTime'] = new Date(new Date(session[s]["endTime"]).getTime()).toLocaleString();
      that.sessionData.push(session[s]);
    }
    console.log(that.sessionData);
    setTimeout(()=>{
      that.lctrl.dismiss();
    }, 1000);
  }

}



